import {
  promised
} from "q";

export class Search {
  constructor(query) {
    this.query = query;
  }
  getResults() {
    if (this.query) {
      let URL = `${location.origin}/search/?searchString=${this.query}`;
      return fetch(URL)
        .then(function (response) {
          return response.json()
        })
        .then((function (recipes) {
          this.recipes = recipes;
        }).bind(this))
        .catch(function (error) {
          alert(error.message)
        });
    }
  }

}